import React from "react";
import { Button, Form } from "react-bootstrap"
import { connect } from "react-redux"
import { UPDATE_SALES_DATE_ACTION, UPDATE_GET_SALES_LIST_ACTION, GET_SALES_LIST_ACTION } from "../../redux/actions/actions";
import { MdCalendarToday, MdKeyboardArrowDown } from "react-icons/md";
import { DateRangePicker } from 'react-date-range';
import Popup from 'reactjs-popup';



class SalesDateSelector extends React.Component {
    state = {
        startDate: new Date(this.props.sales_date.start_date),
        endDate: new Date(this.props.sales_date.end_date),
        key: 'selection',
        date: true,
        cost: false,
    }

    static getDerivedStateFromProps = (props, state) => {

        state.startDate = new Date(props.sales_date.start_date)
        state.endDate = new Date(props.sales_date.end_date)
        return state
    }
    handleSelect = async (ranges) => {

        this.setState({
            startDate: ranges.selection.startDate,
            endDate: ranges.selection.endDate
        })

        const dates = {
            start_date: ranges.selection.startDate,
            end_date: ranges.selection.endDate
        }
        await this.props.UPDATE_GET_SALES_LIST_ACTION()
        await this.props.UPDATE_SALES_DATE_ACTION(dates)
        await this.props.GET_SALES_LIST_ACTION(dates.start_date, dates.end_date)
    }

    render() {

        const start_date = this.state.startDate
        const end_date = this.state.endDate
        return (
            <div>
                <div className="sales-top">

                    <Popup trigger={
                        <Button className='input-dates' size='sm'>
                            <div className='input-display'>
                                <span><MdCalendarToday color='#D04D30' size={18} /></span>
                                {start_date.toDateString()} - {end_date.toDateString()}
                                <span><MdKeyboardArrowDown size={18} /></span>
                            </div>

                        </Button>
                    }
                        position="bottom"
                    >
                        <DateRangePicker
                            onChange={this.handleSelect}
                            showSelectionPreview={true}
                            moveRangeOnFirstSelection={false}
                            months={2}
                            ranges={[this.state]}
                            direction="horizontal"
                            preventSnapRefocus={true}
                            calendarFocus="backwards"
                            className='date-range'
                        />
                    </Popup>



                </div>

            </div>

        )
    }
}

const mapStateToProps = state => {
    const dates = {
        start_date: new Date(2022, 3, 20),
        end_date: new Date(2022, 3, 30)
    }
    return {
        sales_date: (state.update_reducer.sales_date ? state.update_reducer.sales_date : dates),
        depots: state.list_reducer.depot_list,

    };
};

const mapDispatchToProps = {
    UPDATE_SALES_DATE_ACTION,
    UPDATE_GET_SALES_LIST_ACTION,
    GET_SALES_LIST_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(SalesDateSelector);
