import React from 'react';

import { Button, Table, Form, FormControl, Pagination, Alert } from 'react-bootstrap';
import { connect } from 'react-redux';


import { GET_SALES_LIST_ACTION } from "../../redux/actions/actions"
import SpinnerComponent from "../utils/spinner"
import Download from './download';

class SalesTable extends React.Component {
    state = {
        sales_list: this.props.sales_list !== undefined ? this.props.sales_list : [],
        sales_date: this.props.sales_date,
        depot: "All",
        product: "All",
        order_no: null,
        active: 1,
        spinner: false,
        success: true,
        delete_order: null,
    }

    onSearchChange = (e) => {
        let NUM_ORDERS = 50

        let active = this.state.active
        const start_index = (active - 1) * NUM_ORDERS
        const end_index = (active * NUM_ORDERS)
        if (e === "" || e === undefined || e === null) {
            this.setState({ sales_list: this.props.sales_list.slice(start_index, end_index) })
        } else {

            this.setState({ order_no: e })
        }
    }

    onSearch = (e) => {
        e.preventDefault()

        const { order_no } = this.state

        let sales = this.props.sales_list
        sales = sales.filter(function (or) {
            return or.order_no.includes(order_no)
        });

        this.setState({ sales_list: sales, product: "All", depot: "All" })
    }

    static getDerivedStateFromProps = (props, state) => {
        state.success = true
        // state.sales_list = props.sales_list
        // if (props.update_get_sales_list === false) {
        //     state.sales_list = props.sales_list !== undefined ? props.sales_list : []
        // }

        return state
    }

    numberWithCommas(x) {
        try {
            return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        } catch (e) {
            return x
        }

    }

    handleCalc = () => {
        let sales = this.state.sales_list
        let total_vol = 0
        let total_amount = 0

        for (let i = 0; i < sales.length; i++) {
            let sale = sales[i]
            total_vol = total_vol + sale.vol_obs
            total_amount = total_amount + (sale.vol_obs * sale.selling_price)
        }

        return [total_amount, total_vol]
    }
    handleProductChange = async (product) => {
        let sales = this.props.sales_list
        if (product !== "All") {
            sales = sales.filter(function (or) {
                return or.product.name === product
            });
        }


        if (this.state.depot !== "All") {
            const name = this.props.depots[parseInt(this.state.depot)].name
            sales = sales.filter(function (or) {
                return or.depot.name === name
            });
        }

        this.setState({ sales_list: sales, product: product })

    }

    handleDepotChange = (idx) => {
        let sales = this.props.sales_list

        if (idx !== "All") {
            const depot = this.props.depots[parseInt(idx)].name
            sales = sales.filter(function (or) {
                return or.depot.name === depot
            });
        }

        const product = this.state.product
        console.log(product)
        if (product !== "All") {
            sales = sales.filter(function (or) {
                return or.product.name === product
            });
        }
        this.setState({ sales_list: sales, depot: idx })

    }

    formatData = () => {
        let data = this.state.sales_list
        let ndata = []

        const columns = [
            {
                title: "DATE",
                width: { wpx: 80 },
                height: { hpx: 50 },
                style: {
                    font: { bold: true }, fill: { patternType: "solid", fgColor: { rgb: "548235" } }, border: {
                        bottom: { style: 'thin', color: { rgb: '000000' } },
                        top: { style: 'thin', color: { rgb: '000000' } },
                        right: { style: 'thin', color: { rgb: '000000' } },
                        left: { style: 'thin', color: { rgb: '000000' } },
                    }
                },
            },
            {
                title: "DEPOT",
                width: { wpx: 100 },
                height: { hpx: 50 },
                style: {
                    font: { bold: true }, fill: { patternType: "solid", fgColor: { rgb: "548235" } }, border: {
                        bottom: { style: 'thin', color: { rgb: '000000' } },
                        top: { style: 'thin', color: { rgb: '000000' } },
                        right: { style: 'thin', color: { rgb: '000000' } },
                        left: { style: 'thin', color: { rgb: '000000' } },
                    }
                },
            },
            {
                title: "PRODUCT",
                width: { wpx: 80 },
                height: { hpx: 50 },
                style: {
                    font: { bold: true }, fill: { patternType: "solid", fgColor: { rgb: "548235" } }, border: {
                        bottom: { style: 'thin', color: { rgb: '000000' } },
                        top: { style: 'thin', color: { rgb: '000000' } },
                        right: { style: 'thin', color: { rgb: '000000' } },
                        left: { style: 'thin', color: { rgb: '000000' } },
                    }
                },
            },
            {
                title: "CUSTOMER",
                width: { wpx: 180 },
                height: { hpx: 50 },
                style: {
                    font: { bold: true }, fill: { patternType: "solid", fgColor: { rgb: "548235" } }, border: {
                        bottom: { style: 'thin', color: { rgb: '000000' } },
                        top: { style: 'thin', color: { rgb: '000000' } },
                        right: { style: 'thin', color: { rgb: '000000' } },
                        left: { style: 'thin', color: { rgb: '000000' } },
                    }
                },
            },
            {
                title: "TRUCK",
                width: { wpx: 100 },
                height: { hpx: 50 },
                style: {
                    font: { bold: true }, fill: { patternType: "solid", fgColor: { rgb: "548235" } }, border: {
                        bottom: { style: 'thin', color: { rgb: '000000' } },
                        top: { style: 'thin', color: { rgb: '000000' } },
                        right: { style: 'thin', color: { rgb: '000000' } },
                        left: { style: 'thin', color: { rgb: '000000' } },
                    }
                },
            },

            {
                title: "DRIVER",
                width: { wpx: 150 },
                height: { hpx: 50 },
                style: {
                    font: { bold: true }, fill: { patternType: "solid", fgColor: { rgb: "548235" } }, border: {
                        bottom: { style: 'thin', color: { rgb: '000000' } },
                        top: { style: 'thin', color: { rgb: '000000' } },
                        right: { style: 'thin', color: { rgb: '000000' } },
                        left: { style: 'thin', color: { rgb: '000000' } },
                    }
                },
            },
            {
                title: "ORDER NO",
                width: { wpx: 100 },
                height: { hpx: 50 },
                style: {
                    font: { bold: true }, fill: { patternType: "solid", fgColor: { rgb: "548235" } }, border: {
                        bottom: { style: 'thin', color: { rgb: '000000' } },
                        top: { style: 'thin', color: { rgb: '000000' } },
                        right: { style: 'thin', color: { rgb: '000000' } },
                        left: { style: 'thin', color: { rgb: '000000' } },
                    }
                },
            },
            {
                title: "ENTRY NO",
                width: { wpx: 150 },
                height: { hpx: 50 },
                style: {
                    font: { bold: true }, fill: { patternType: "solid", fgColor: { rgb: "548235" } }, border: {
                        bottom: { style: 'thin', color: { rgb: '000000' } },
                        top: { style: 'thin', color: { rgb: '000000' } },
                        right: { style: 'thin', color: { rgb: '000000' } },
                        left: { style: 'thin', color: { rgb: '000000' } },
                    }
                },
            },
            {
                title: "LPO NO",
                width: { wpx: 120 },
                height: { hpx: 50 },
                style: {
                    font: { bold: true }, fill: { patternType: "solid", fgColor: { rgb: "548235" } }, border: {
                        bottom: { style: 'thin', color: { rgb: '000000' } },
                        top: { style: 'thin', color: { rgb: '000000' } },
                        right: { style: 'thin', color: { rgb: '000000' } },
                        left: { style: 'thin', color: { rgb: '000000' } },
                    }
                },
            },
            {
                title: "VOL OBS",
                width: { wpx: 80 },
                height: { hpx: 50 },
                style: {
                    font: { bold: true }, fill: { patternType: "solid", fgColor: { rgb: "548235" } }, border: {
                        bottom: { style: 'thin', color: { rgb: '000000' } },
                        top: { style: 'thin', color: { rgb: '000000' } },
                        right: { style: 'thin', color: { rgb: '000000' } },
                        left: { style: 'thin', color: { rgb: '000000' } },
                    }
                },
            },
            {
                title: "VOL 20",
                width: { wpx: 80 },
                height: { hpx: 50 },
                style: {
                    font: { bold: true }, fill: { patternType: "solid", fgColor: { rgb: "548235" } }, border: {
                        bottom: { style: 'thin', color: { rgb: '000000' } },
                        top: { style: 'thin', color: { rgb: '000000' } },
                        right: { style: 'thin', color: { rgb: '000000' } },
                        left: { style: 'thin', color: { rgb: '000000' } },
                    }
                },
            },
            {
                title: "SELLING PRICE",
                width: { wpx: 100 },
                height: { wpx: 50 },
                style: {
                    font: { bold: true }, fill: { patternType: "solid", fgColor: { rgb: "548235" } }, border: {
                        bottom: { style: 'thin', color: { rgb: '000000' } },
                        top: { style: 'thin', color: { rgb: '000000' } },
                        right: { style: 'thin', color: { rgb: '000000' } },
                        left: { style: 'thin', color: { rgb: '000000' } },
                    }
                },
            },

            {
                title: "AMOUNT",
                width: { wpx: 100 },
                height: { hpx: 50 },
                style: {
                    font: { bold: true }, fill: { patternType: "solid", fgColor: { rgb: "548235" } }, border: {
                        bottom: { style: 'thin', color: { rgb: '000000' } },
                        top: { style: 'thin', color: { rgb: '000000' } },
                        right: { style: 'thin', color: { rgb: '000000' } },
                        left: { style: 'thin', color: { rgb: '000000' } },
                    }
                },
            },

            {
                title: "PAID",
                width: { wpx: 50 },
                height: { hpx: 50 },
                style: {
                    font: { bold: true }, fill: { patternType: "solid", fgColor: { rgb: "548235" } }, border: {
                        bottom: { style: 'thin', color: { rgb: '000000' } },
                        top: { style: 'thin', color: { rgb: '000000' } },
                        right: { style: 'thin', color: { rgb: '000000' } },
                        left: { style: 'thin', color: { rgb: '000000' } },
                    }
                },
            },

        ]

        for (let i = 0; i < data.length; i++) {
            const sale = data[i]
            ndata.push([
                {
                    value: sale.date,
                    style: {
                        fill: { patternType: "solid", fgColor: { rgb: "B4C6E7" } },
                        border: {
                            bottom: { style: 'thin', color: { rgb: '000000' } },
                            top: { style: 'thin', color: { rgb: '000000' } },
                            right: { style: 'thin', color: { rgb: '000000' } },
                            left: { style: 'thin', color: { rgb: '000000' } },
                        }
                    },
                },
                {
                    value: sale.depot.name,
                    style: {
                        fill: { patternType: "solid", fgColor: { rgb: "B4C6E7" } },
                        border: {
                            bottom: { style: 'thin', color: { rgb: '000000' } },
                            top: { style: 'thin', color: { rgb: '000000' } },
                            right: { style: 'thin', color: { rgb: '000000' } },
                            left: { style: 'thin', color: { rgb: '000000' } },
                        }
                    },
                },
                {
                    value: sale.product.name,
                    style: {
                        fill: { patternType: "solid", fgColor: { rgb: "B4C6E7" } },
                        border: {
                            bottom: { style: 'thin', color: { rgb: '000000' } },
                            top: { style: 'thin', color: { rgb: '000000' } },
                            right: { style: 'thin', color: { rgb: '000000' } },
                            left: { style: 'thin', color: { rgb: '000000' } },
                        }
                    },
                },


                {
                    value: sale.customer.name,
                    style: {
                        fill: { patternType: "solid", fgColor: { rgb: "7B7B7B" } },
                        border: {
                            bottom: { style: 'thin', color: { rgb: '000000' } },
                            top: { style: 'thin', color: { rgb: '000000' } },
                            right: { style: 'thin', color: { rgb: '000000' } },
                            left: { style: 'thin', color: { rgb: '000000' } },
                        }
                    },
                },
                {
                    value: sale.truck.plate_no === null ? "" : sale.truck.plate_no,
                    style: {
                        fill: { patternType: "solid", fgColor: { rgb: "7B7B7B" } },
                        border: {
                            bottom: { style: 'thin', color: { rgb: '000000' } },
                            top: { style: 'thin', color: { rgb: '000000' } },
                            right: { style: 'thin', color: { rgb: '000000' } },
                            left: { style: 'thin', color: { rgb: '000000' } },
                        }
                    },
                },
                {
                    value: sale.truck.driver === null ? "" : sale.truck.driver,
                    style: {
                        fill: { patternType: "solid", fgColor: { rgb: "7B7B7B" } },
                        border: {
                            bottom: { style: 'thin', color: { rgb: '000000' } },
                            top: { style: 'thin', color: { rgb: '000000' } },
                            right: { style: 'thin', color: { rgb: '000000' } },
                            left: { style: 'thin', color: { rgb: '000000' } },
                        }
                    },
                },

                {
                    value: sale.order_no === null ? "" : sale.order_no,
                    style: {
                        fill: { patternType: "solid", fgColor: { rgb: "A9D08E" } },
                        border: {
                            bottom: { style: 'thin', color: { rgb: '000000' } },
                            top: { style: 'thin', color: { rgb: '000000' } },
                            right: { style: 'thin', color: { rgb: '000000' } },
                            left: { style: 'thin', color: { rgb: '000000' } },
                        }
                    },
                },
                {
                    value: sale.entry_no === null ? "" : sale.entry_no,
                    style: {
                        fill: { patternType: "solid", fgColor: { rgb: "A9D08E" } },
                        border: {
                            bottom: { style: 'thin', color: { rgb: '000000' } },
                            top: { style: 'thin', color: { rgb: '000000' } },
                            right: { style: 'thin', color: { rgb: '000000' } },
                            left: { style: 'thin', color: { rgb: '000000' } },
                        }
                    },
                },
                {
                    value: sale.lpo_no === null ? "" : sale.lpo_no,
                    style: {
                        fill: { patternType: "solid", fgColor: { rgb: "A9D08E" } },
                        border: {
                            bottom: { style: 'thin', color: { rgb: '000000' } },
                            top: { style: 'thin', color: { rgb: '000000' } },
                            right: { style: 'thin', color: { rgb: '000000' } },
                            left: { style: 'thin', color: { rgb: '000000' } },
                        }
                    },
                },

                {
                    value: sale.vol_obs,
                    style: {
                        fill: { patternType: "solid", fgColor: { rgb: "F8CBAD" } },
                        border: {
                            bottom: { style: 'thin', color: { rgb: '000000' } },
                            top: { style: 'thin', color: { rgb: '000000' } },
                            right: { style: 'thin', color: { rgb: '000000' } },
                            left: { style: 'thin', color: { rgb: '000000' } },
                        }
                    },
                },
                {
                    value: sale.vol_20 === null ? 0 : sale.vol_20,
                    style: {
                        fill: { patternType: "solid", fgColor: { rgb: "F8CBAD" } },
                        border: {
                            bottom: { style: 'thin', color: { rgb: '000000' } },
                            top: { style: 'thin', color: { rgb: '000000' } },
                            right: { style: 'thin', color: { rgb: '000000' } },
                            left: { style: 'thin', color: { rgb: '000000' } },
                        }
                    },
                },
                {
                    value: parseFloat(sale.selling_price),
                    style: {
                        fill: { patternType: "solid", fgColor: { rgb: "F8CBAD" } },
                        border: {
                            bottom: { style: 'thin', color: { rgb: '000000' } },
                            top: { style: 'thin', color: { rgb: '000000' } },
                            right: { style: 'thin', color: { rgb: '000000' } },
                            left: { style: 'thin', color: { rgb: '000000' } },
                        },
                        alignment: { horizontal: "right" },
                    }
                },
                {
                    value: parseFloat(sale.vol_obs * sale.selling_price),
                    style: {
                        fill: { patternType: "solid", fgColor: { rgb: "00B050" } },
                        border: {
                            bottom: { style: 'thin', color: { rgb: '000000' } },
                            top: { style: 'thin', color: { rgb: '000000' } },
                            right: { style: 'thin', color: { rgb: '000000' } },
                            left: { style: 'thin', color: { rgb: '000000' } },
                        },
                        alignment: { horizontal: "right" },
                    }
                },
                {
                    value: sale.is_paid,
                    style: {
                        fill: { patternType: "solid", fgColor: { rgb: "A9D08E" } },
                        border: {
                            bottom: { style: 'thin', color: { rgb: '000000' } },
                            top: { style: 'thin', color: { rgb: '000000' } },
                            right: { style: 'thin', color: { rgb: '000000' } },
                            left: { style: 'thin', color: { rgb: '000000' } },
                        }
                    },
                },
            ])

        }
        return [{ columns: columns, data: ndata }]
    }

    render() {
        let items = [];
        const NUM_ORDERS = 50
        let active = this.state.active;
        const start_index = (active - 1) * NUM_ORDERS
        const end_index = active * NUM_ORDERS

        for (let number = 1; number <= Math.ceil(this.state.sales_list.length / NUM_ORDERS); number++) {
            items.push(
                <Pagination.Item key={number} active={number === active} onClick={() => this.setState({ active: number })}>
                    {number}
                </Pagination.Item>,
            );
        }

        const { depots, products } = this.props
        const totals = this.handleCalc()
        const format = this.formatData()
        return (
            <div>
                {
                    this.state.spinner ? <SpinnerComponent spinner={this.state.spinner} /> :
                        <div className='sale-table'>

                            <div className='sale-table__header'>

                                <Form className="d-flex" style={{ alignItems: "flex-end" }} onSubmit={this.onSearch}>
                                    <Download dataset={format} />
                                    <Form.Group className="mb-4 pie-select__child sale-table__select" controlId="exampleForm.ControlInput1" style={{ marginBottom: 0 }}>
                                        <Form.Label>Depot</Form.Label>
                                        <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.handleDepotChange(e.target.value)}>
                                            {this.state.depot === "All" ? null : <option value={this.state.depot}>{depots[parseInt(this.state.depot)].name}</option>}
                                            <option value={"All"}>All</option>

                                            {depots.map((depot, idx) =>
                                                this.state.depot === idx ? null : <option key={idx} value={idx}>{depot.name}</option>
                                            )}

                                        </Form.Select>
                                    </Form.Group>

                                    <Form.Group className="mb-4 pie-select__child sale-table__select" controlId="exampleForm.ControlInput1" style={{ marginBottom: 0 }}>
                                        <Form.Label>Product</Form.Label>
                                        <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.handleProductChange(e.target.value)}>
                                            {this.state.product === "All" ? null : <option value={this.state.product}>{this.state.product}</option>}
                                            <option value={"All"}>All</option>

                                            {products.map((product, idx) =>
                                                this.state.product === product.name ? null : <option key={idx} value={product.name}>{product.name}</option>
                                            )}

                                        </Form.Select>
                                    </Form.Group>


                                    <FormControl
                                        type="search"
                                        placeholder="Search by order no."
                                        className="me-2 sales-table__search-input"
                                        aria-label="Search by order no."
                                        size='sm'
                                        onChange={(e) => this.onSearchChange(e.target.value)}
                                    />
                                    <Button variant="outline-primary" type='submit' >Search</Button>
                                </Form>
                            </div>

                            <Table striped bordered hover size="sm">
                                <thead>
                                    <tr>
                                        <th></th>
                                        <th>Date</th>
                                        {this.props.from_customer ? null :
                                            <th>Depot</th>
                                        }
                                        <th>Product</th>
                                        {this.props.from_customer ? null :
                                            <th>Customer</th>
                                        }
                                        <th>Truck</th>
                                        <th>Driver</th>
                                        <th>Order No</th>
                                        <th>Lpo No</th>
                                        <th>Entry No</th>
                                        <th>S.Price</th>
                                        <th>OBS Vol</th>
                                        <th>Vol@20</th>
                                        <th>Amount</th>
                                        <th>Paid</th>
                                        {/* <th>Edit</th>
                            <th>Del</th> */}
                                    </tr>
                                </thead>
                                <tbody>
                                    {this.state.sales_list.slice(start_index, end_index).map((sale, idx) =>
                                        <tr key={idx}>
                                            <td>{start_index + idx + 1}</td>
                                            <td>{sale.date}</td>
                                            {this.props.from_customer ? null :
                                                <td>{sale.depot.name}</td>
                                            }
                                            <td>{sale.product.name}</td>
                                            {this.props.from_customer ? null :
                                                <td>{sale.customer.name}</td>
                                            }
                                            <td>{sale.truck.plate_no}</td>
                                            <td>{sale.truck.driver}</td>
                                            <td>{sale.order_no}</td>
                                            <td>{sale.lpo_no}</td>
                                            <td>{sale.entry_no}</td>
                                            <td>{sale.selling_price}</td>
                                            <td>{this.numberWithCommas(sale.vol_obs)}</td>
                                            <td>{this.numberWithCommas(sale.vol_20)}</td>
                                            <td>{this.numberWithCommas(sale.vol_obs * sale.selling_price)}</td>
                                            <td>{sale.is_paid ? "True" : "False"}</td>
                                            {/* <td>
                                    <Popup
                                        contentStyle={{ width: 500, minHeight: 400 }}
                                        trigger={
                                            <Button variant='transparent' className='sales-table__action-button'>
                                                <MdEditNote color='blue' size={20} />
                                            </Button>
                                        }
                                        position="left top"
                                    >


                                    </Popup>
                                </td>
                                <td>
                                    <Popup
                                        contentStyle={{ width: 500, minHeight: 150 }}
                                        onOpen={() => this.setState({ success: false })}
                                        trigger={
                                            <Button variant='transparent' className='action-button' onClick={() => this.setState({ success: false })}>
                                                <MdDelete color='red' size={20} />
                                            </Button>
                                        }
                                        position="left top"
                                    >


                                    </Popup>
                                </td> */}
                                        </tr>
                                    )}
                                    <tr>
                                        <th></th>
                                        <th>Totals</th>
                                        <th></th>
                                        <th></th>
                                        {this.props.from_customer ? null :
                                            <th></th>
                                        }
                                        {this.props.from_customer ? null :
                                            <th></th>
                                        }
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                        <th>Kshs. {this.numberWithCommas(totals[1])}</th>
                                        <th></th>
                                        <th>Kshs. {this.numberWithCommas(totals[0])}</th>
                                        <th></th>
                                        {/* <th>Edit</th>
                            <th>Del</th> */}
                                    </tr>
                                </tbody>
                            </Table>
                            <div>


                                <Pagination size="sm">{items}</Pagination>
                            </div>

                        </div>
                }
            </div>
        )
    }
}

const mapStateToProps = state => {
    const dates = {
        start_date: new Date(2022, 1, 20),
        end_date: new Date(2022, 1, 28)
    }
    return {
        // sales_list: (state.list_reducer.sales_list ? state.list_reducer.sales_list : []),
        sales_date: (state.update_reducer.sales_date ? state.update_reducer.sales_date : dates),
        update_get_sales_list: state.list_reducer.update_get_sales_list,
        depots: state.list_reducer.depot_list,
        products: state.list_reducer.product_list,
    };
};

const mapDispatchToProps = {
    GET_SALES_LIST_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(SalesTable);
